*{
  margin: 0;
  padding: 0;
}
#app{
  height: 100vh;
}
.App {
  /* color: violet; */
  text-align: center;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center; 
  height: 100vh;
}

.App-header {
  background-color: #282c34;
  color: white;
  width: 100%;
  margin: 20px;
}

.theme-btn{
  margin: 5px;
  cursor: pointer;
  border-radius: 50px;
  width: 120px;
  height: 20px;
  align-self: flex-end;
}

.gameboard{
  margin-top: 15px;
  border: 2px solid white;
  height: 300px;
  width: 300px;
}

.square{
  width: 100px;
  height: 100px;
  font-size: 50px;
  margin: 0;
  padding: 0;
  font-weight: bold;
} 

.squarerow{
  display: block;
  flex-direction: row;
}  
.img{
  position: fixed;
  z-index: 100;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  bottom: 10px;
  left: 10px;
}
img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}